.chatbot {
  font-family: monospace;
  background: #f5f8fb;
  text-align: center;
  display: flex;
  flex-flow: column;
  width: 375px;
  overflow: hidden;
}

.header {
  padding: 20px;
  text-align: center;
  color: #fff;
  max-height: 58px;
  font-size: 16px;
  font-weight: bold;
  background-color: var(--brand-primary-color, #0644a7);
}

.messages {
  width: 100%;
  height: 400px;
  overflow: auto;
  display: flex;
  flex-direction: column;
  padding: 10px 0px;
}

.message-container {
  width: 100%;
}

.bot-message {
  float: left;
  text-align: start;
  padding: 15px 20px;
  margin: 5px;
  border-radius: 20px 20px 20px 1px;
  background: var(--brand-primary-color, #0644a7);
  color: #fff;
  min-width: 40px;
}

.user-message {
  float: right;
  text-align: start;
  padding: 15px 10px;
  margin: 10px;
  border-radius: 20px 20px 1px 20px;
  background: #cccccc;
  color: black;
}

.input {
  position: relative;
}

.input > form > input[type="text"] {
  font-family: monospace;
  font-size: 16px;
  border: 0;
  border-radius: 0;
  border-top: 1px solid #eee;
  box-shadow: none;
  box-sizing: border-box;
  opacity: 1;
  outline: none;
  padding: 16px 52px 16px 10px;
  width: 100%;
}

.input > form > input:focus {
  outline: none;
}

.input > form > button {
  background-color: transparent;
  border: 0;
  border-bottom-right-radius: 10px;
  box-shadow: none;
  cursor: pointer;
  fill: var(--brand-primary-color, #0644a7);
  opacity: 1;
  outline: none;
  padding: 14px 16px 12px 16px;
  position: absolute;
  right: 0;
  top: 0;
}

html, body {
  margin: 0;
  background-color: transparent;
}